.error_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 600px;
    text-align: center;
}

.numberError {
    font-size: 150px;
    font-weight: 600;
    text-shadow: -1px -1px #000,
    0 1px 0 #444;
    color: #222;
}

.messageError {
    font-size: 30px;
    color: #000000;
}

.redirectionText {
    padding: 5px 0;
}

.link {
    color: red;
    border-bottom: 1px solid white;
}